<template>
	<div>
		<el-divider content-position="left">国内航班</el-divider>
		<el-card shadow="never">
			<div style="text-align: right;width: 100%;">
				<el-radio-group v-model="hotCity" size="small" @change="search">
					<el-radio-button label="上海"></el-radio-button>
					<el-radio-button label="北京"></el-radio-button>
					<el-radio-button label="广州"></el-radio-button>
					<el-radio-button label="深圳"></el-radio-button>
				</el-radio-group>
			</div>
			<el-row :gutter="12">
				<el-col :span="8" v-for="plane in this.chinaPlane">
					<el-popover placement="right" width="430" trigger="click">
						<el-row :gutter="20">
							<el-col :span="12">
								<el-col>起飞机场：{{ plane.startAirport }}</el-col>
								<el-col>降落机场：{{ plane.endAirport }}</el-col>
								<el-col>客机机型：{{ plane.model }}</el-col>
								<el-col v-if="plane.type===0">客机型号：小型</el-col>
								<el-col v-else-if="plane.type===1">客机型号：中型</el-col>
								<el-col v-else-if="plane.type===2">客机型号：大型</el-col>
								<el-col>客机序号：{{ plane.number }}</el-col>
							</el-col>
							<el-col :span="12">
								<el-col>
									<h3>{{ plane.name }}</h3><img :src="plane.icon">
								</el-col>
								<el-button @click="addClock(plane)" size="mini">添加提醒<i class="el-icon-alarm-clock"></i></el-button>
							</el-col>
						</el-row>
						<el-card shadow="hover" slot="reference">
							<el-row :gutter="20">
								<el-col :span="12">{{ plane.startCity }} <i class="el-icon-caret-right"></i> {{ plane.endCity }}</el-col>
								<el-col :span="12">
									<h4 style="color: indianred;float: right;">{{ plane.price }} ￥起</h4>
								</el-col>
								<el-col>{{ plane.startTime }}</el-col>
							</el-row>
						</el-card>
					</el-popover>
				</el-col>
			</el-row>
		</el-card>
		<el-dialog title="添加邮箱提醒" :visible.sync="dialogFormVisible">
			<el-form :model="clockForm" :rules="rules" ref="clockForm">
				<el-form-item label="邮箱" prop="email" :label-width="formLabelWidth">
					<el-row :gutter="20">
						<el-col :span="10">
							<el-input v-model="clockForm.email" autocomplete="off"></el-input>
						</el-col>
						<el-col :span="10">
							<el-button :type="send.sendType" :model="send" :disabled="send.sendStatus" @click="sendCode" :key="send.sendMessage">
								{{ send.sendMessage }}
							</el-button>
						</el-col>
					</el-row>
				</el-form-item>
				<el-form-item label="验证码" prop="code" :label-width="formLabelWidth">
					<el-row :gutter="20">
						<el-col :span="10">
							<el-input v-model="clockForm.code" autocomplete="off"></el-input>
						</el-col>
					</el-row>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="closeForm('clockForm')">取 消</el-button>
				<el-button type="primary" @click="submitForm('clockForm')">添 加</el-button>
			</div>
		</el-dialog>

	</div>
</template>

<script>
	export default {
		name: 'ChinaPlane',
		data() {
			return {
				userCity: '武汉',
				hotCity: '深圳',
				gridData: '',
				chinaPlane: '',
				dialogTableVisible: false,
				dialogFormVisible: false,
				send: {
					sendType: '',
					sendStatus: false,
					sendMessage: '发送验证码',
				},
				clockForm: {},
				rules: {
					email: [{
						required: true,
						type: 'email',
						message: '请输入正确的邮箱地址',
						trigger: ['blur', 'change']
					}],
					code: [{
						required: true,
						message: '请输入正确的验证码',
						trigger: ['blur', 'change']
					}]
				},
				formLabelWidth: '120px'
			};
		},
		created() {
			this.search()
		},
		methods: {
			search() {
				var _this = this;
				this.$axios
					.post('/china_plane', {
						currentPage: 1,
						pageSize: 9,
						startCity: this.userCity,
						endCity: this.hotCity,
						orderByField: 'start_time'
					})
					.then(resp => {
						if (resp.data.code === 200) {
							_this.chinaPlane = resp.data.data;
						}
					})
					.catch(failResponse => {})
			},
			sendCode() {
				//发送验证码
				this.$axios
					.post('/send', {
						email: this.clockForm.email
					})
					.then(resp => {
						if (resp.data.code === 200) {
							// if (resp.data.data.return_code == "00000") {
							this.$message({
								type: 'success',
								message: '验证码发送成功，请注意接收'
							})
							this.send.sendType = "success";
							this.send.sendStatus = "disabled";
							this.setInterval();
							// }else{
							// 	this.$message.error("验证码发送错误，错误代码:"+resp.data.data.return_code);
							// }
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
					})
			},
			addClock(plane) {
				if(new Date().getTime() > new Date(this.$route.query.startTime).getTime()){
					this.$message.error("该航班已经起飞，请注意时间");
						return false;
				}
				this.$axios
					.get('/authentication')
					.then(resp => {
						//直接添加提醒
						if (resp.data.code === 200) {
							var data = {
								fightId: plane.fid,
								number: plane.number,
								status: 0,
								type: 0
							}
							this.$axios
								.post('/clock', data)
								.then(resp => {
									if (resp.data.code === 200) {
										this.$message({
											type: 'success',
											message: resp.data.msg
										})
									} else {
										this.$message.error("发生错误，请联系管理员");
									}
								})
							//未绑定邮箱
						} else if (resp.data.code === 400) {
							this.dialogTableVisible = true;
							this.dialogFormVisible = true;
							this.clockForm.plane = plane;
						} else {
							this.$message.error("请先登录");
						}
					})
			},
			submitForm(formName) {
				if(Number.parseInt(this.clockForm.code)=="NaN"){
					this.$message.error("请填入正确的验证码格式");
					return false
				}
				this.$axios
					.post('/email',{email:this.clockForm.email,code:this.clockForm.code})
					.then(resp => {
						if (resp.data.code === 200) {
						var data = {
							fightId: this.clockForm.plane.fid,
							number: this.clockForm.plane.number,
							status: 0,
							type: 0
						}
						this.$refs[formName].validate((valid) => {
							if (valid) {
								//提交表单
								this.$axios
									.post('/clock', data)
									.then(resp => {
										if (resp.data.code === 200) {
											this.$message({
												type: 'success',
												message: resp.data.msg
											})
										} else {
											this.$message.error("发生错误，请联系管理员");
										}
									})
								//添加成功
								this.closeForm(formName);
							} else {
								return false;
							}
						})
						}  else {
							this.$message.error(resp.data.msg);
						}
					})
			
			},
			closeForm(formName) {
				clearInterval(this.interval);
				this.dialogFormVisible = false;
				this.send.sendType = "";
				this.send.sendStatus = false;
				this.send.sendMessage = "发送验证码";
			},
			setInterval() {
				var time = 60;
				this.interval = setInterval(() => {
					this.send.sendMessage = time;
					time -= 1;
					if (time === -1) {
						clearInterval(this.interval);
						this.send.sendType = "";
						this.send.sendStatus = false;
						this.send.sendMessage = "重新发送验证码";
					}
				}, 1000)
			}
		}
	}
</script>

<style scoped="scoped">
	.el-col {
		margin: 5px 0;
	}

	.el-card {
		cursor: pointer;
	}

	img {
		width: 150px;
	}
</style>
