<template>
	<van-tabs v-model="active">
		<van-tab title="国内航班">
			<van-grid :column-num="3" :border="false">
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
				<van-grid-item>
					<van-icon name="exchange" />
				</van-grid-item>
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
			</van-grid>
			<van-grid :column-num="1" :border="false">
				<van-grid-item>
					<DateTimePicker></DateTimePicker>
				</van-grid-item>
				<van-grid-item>
					<van-button icon="search" round type="info">搜索</van-button>
				</van-grid-item>
			</van-grid>
		</van-tab>
		<van-tab title="国外航班">
			<van-grid :column-num="3" :border="false">
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
				<van-grid-item>
					<van-icon name="exchange" />
				</van-grid-item>
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
			</van-grid>
			<van-grid :column-num="1" :border="false">
				<van-grid-item>
					<DateTimePicker></DateTimePicker>
				</van-grid-item>
				<van-grid-item>
					<van-button icon="search" round type="info">搜索</van-button>
				</van-grid-item>
			</van-grid>
		</van-tab>
		<van-tab title="新闻资讯">
			<van-grid :column-num="3" :border="false">
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
				<van-grid-item>
					<van-icon name="exchange" />
				</van-grid-item>
				<van-grid-item>
					<ChinaCitySelect></ChinaCitySelect>
				</van-grid-item>
			</van-grid>
			<van-grid :column-num="1" :border="false">
				<van-grid-item>
					<DateTimePicker></DateTimePicker>
				</van-grid-item>
				<van-grid-item>
					<van-button icon="search" round type="info">搜索</van-button>
				</van-grid-item>
			</van-grid>
		</van-tab>
	</van-tabs>
</template>

<script>
	import ChinaCitySelect from '@/components/mobile/comm/ChinaCitySelect.vue'
	import DateTimePicker from '@/components/mobile/comm/DateTimePicker.vue'
	export default {
		name: 'Search',
		components: {
			DateTimePicker,
			ChinaCitySelect
		},
		data() {
			return {
				list: [],
				show: false,
				actions: [{
						name: '登录'
					},
					{
						name: '选项'
					},
					{
						name: '选项',
						subname: '描述信息'
					}
				]
			};
		},

		methods: {

			// position 为关闭时点击的位置
			// instance 为对应的 SwipeCell 实例
			addClock({
				position,
				instance
			}) {
				switch (position) {
					case 'left':
					case 'cell':
					case 'outside':
						instance.close();
						break;
					case 'right':
						Dialog.confirm({
							message: '确定删除吗？'
						}).then(() => {
							instance.close();
						});
						break;
				}
			},
		}
	}
</script>

<style scoped="scoped">
	*{
		font-size: 1.875rem;
		font-weight: 600;
	}
	.van-button__text{
