<template>
	<div id="result">
		<el-row>
			<el-col :span="24">
				<span class="big">搜索：</span><span class="big">{{this.$route.query.startCity}}<i style="display: inline-block;" class="el-icon el-icon-caret-right"></i>
				{{this.$route.query.endCity}}</span>
				<span> {{this.$route.query.startTime}}</span> 所有起飞/到达时间均为当地时间
				<el-card shadow="never">
					<div class="block" style="margin-bottom:-5px;">
						<el-row :gutter="5">
							<el-col :span="3">
								<el-cascader placeholder="中转城市" :options="options" :props="props" collapse-tags clearable></el-cascader>
							</el-col>
							<el-col :span="4">
								<el-cascader placeholder="航空公司" :options="options" :props="props" collapse-tags clearable></el-cascader>
							</el-col>
							<el-col :span="4">
								<el-cascader placeholder="起抵时间" :options="options" :props="props" collapse-tags clearable></el-cascader>
							</el-col>
							<el-col :span="4">
								<el-cascader placeholder="机场" :options="options" :props="props" collapse-tags clearable></el-cascader>
							</el-col>
							<el-col :span="3">
								<el-cascader placeholder="更多" :options="more" :props="props" collapse-tags clearable></el-cascader>
							</el-col>
							<el-col :span="2">
								<el-button>重置</el-button>
							</el-col>
						</el-row>
						<el-row style="margin-bottom:-20px;margin-top: 15px;">
							<el-col :span="2" :offset="6">
								<el-link :underline="false" @click="sortStartTime">
									<i class="el-icon-d-caret"></i>
								</el-link>
							</el-col>
							<el-col :span="2" :offset="4">
								<el-link :underline="false" @click="sortEndTime">
									<i class="el-icon-d-caret"></i>
								</el-link>
							</el-col>
							<el-col :span="3">
								<el-link :underline="false" @click="sortUseTime">
									<i class="el-icon-d-caret"></i>
								</el-link>
							</el-col>
							<el-col :span="3" :offset="3">
								<el-link :underline="false" @click="sortPrice">
									<i class="el-icon-d-caret"></i>
								</el-link>
							</el-col>
						</el-row>
					</div>
				</el-card>
			</el-col>
		</el-row>
		<el-row class="infinite-list" v-infinite-scroll="load" style="overflow-y:scroll;height: 600px;">
			<el-col :span="24" v-if="results.length!=0" v-for="result in results" class="infinite-list-item">
				<el-card shadow="never">
					<el-row :gutter="20">
						<el-col :span="5">
							<el-col :span="7">
								<el-avatar :src="result.icon"></el-avatar>
							</el-col>
							<el-col :span="17">
								<el-col>{{result.name}}</el-col>
								<el-col>{{result.number}} </el-col>
								<el-col>
									<span v-if="result.type==0">小型</span>
									<span v-else-if="result.type==1">中型</span>
									<span v-else-if="result.type==2">大型</span>
								</el-col>
							</el-col>
						</el-col>
						<el-col :span="11">
							<el-col :span="6">
								<el-col>
									<span class="big">{{result.startTime|subString}}</span>
								</el-col>
								<el-col>
									{{result.startAirport}}
								</el-col>
							</el-col>
							<el-col :span="7">
								<img src="@/assets/img/icon/right.png" class="right-icon" />
							</el-col>
							<el-col :span="7">
								<el-col>
									<span class="big">{{ result.endTime|subString}}</span>
								</el-col>
								<el-col>
									{{result.endAirport}}
								</el-col>
							</el-col>
							<el-col :span="4">
								1天
							</el-col>
						</el-col>
						<el-col :span="3">
							<el-col>
								途径城市
							</el-col>
							<el-col>
								<small>上海</small><small>上海</small>
							</el-col>
						</el-col>
						<el-col :span="3" class="red">
							<span class="big">{{result.price}}￥</span>起
						</el-col>
						<el-col :span="2">
							<el-button @click="addClock(result)" size="mini">添加提醒<i class="el-icon-alarm-clock"></i></el-button>
						</el-col>
					</el-row>
				</el-card>
			</el-col>
			<el-col :span="24" v-else>
				<el-card shadow="never">
				没有相关搜索记录
				</el-card>
			</el-col>
		</el-row>
		<el-dialog title="添加邮箱提醒" :visible.sync="dialogFormVisible">
			<el-form :model="clockForm" :rules="rules" ref="clockForm">
				<el-form-item label="邮箱" prop="email" :label-width="formLabelWidth">
					<el-row :gutter="20">
						<el-col :span="10">
							<el-input v-model="clockForm.email" autocomplete="off"></el-input>
						</el-col>
						<el-col :span="10">
							<el-button :type="send.sendType" :model="send" :disabled="send.sendStatus" @click="sendCode" :key="send.sendMessage">
								{{ send.sendMessage }}
							</el-button>
						</el-col>
					</el-row>
				</el-form-item>
				<el-form-item label="验证码" prop="code" :label-width="formLabelWidth">
					<el-row :gutter="20">
						<el-col :span="10">
							<el-input v-model="clockForm.code" autocomplete="off"></el-input>
						</el-col>
					</el-row>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="closeForm('clockForm')">取 消</el-button>
				<el-button type="primary" @click="submitForm('clockForm')">添 加</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: 'SearchResult',
		created() {
			this.$axios
				.post('/query', {
					startCity: this.$route.query.startCity,
					endCity: this.$route.query.endCity,
					startTime: this.$route.query.startTime,
					endTime: this.$route.query.endTime,
					key:this.$route.query.key,
					orderByField:'price',
					isAsc:false
				})
				.then(resp => {
					if (resp.data.code === 200) {
						var data = resp.data.msg;
						this.$message({
							message: data,
							type: 'success'
						});
						this.results = resp.data.data.records
						console.log(this.results);
					}
				})
				.catch(failResponse => {})
		},
		data() {
			return {
				results: '',
				key: '',
				count: 0,
				dialogTableVisible: false,
				dialogFormVisible: false,
				sortStartTimeFlag: false,
				sortEndTimeFlag: false,
				sortUseTimeFlag: false,
				sortPriceFlag: false,
				send: {
					sendType: '',
					sendStatus: false,
					sendMessage: '发送验证码',
				},
				clockForm: {
					email: '',
					code: '',
					plane: ''
				},
				rules: {
					email: [{
						required: true,
						message: '请输入邮箱地址',
						trigger: ['blur', 'change']
					}, {
						type: 'email',
						message: '请输入正确的邮箱地址',
						trigger: ['blur', 'change']
					}],
					code: [{
						required: true,
						message: '请填写验证码'
					}]
				},
				formLabelWidth: '120px',
				props: {
					multiple: true
				},
				options: [{
					value: 1,
					label: '东南',
					children: [{
						value: 2,
						label: '上海'
					}, {
						value: 7,
						label: '江苏'
					}, {
						value: 12,
						label: '浙江'
					}]
				}, {
					value: 17,
					label: '西北',
					children: [{
						value: 18,
						label: '陕西'
					}, {
						value: 21,
						label: '新疆维吾尔族自治区'
					}]
				}],
				more: [{
					value: 0,
					label: '小型'
				}, {
					value: 1,
					label: '中型'
				}, {
					value: 2,
					label: '大型'
				}],
			};
		},
		methods: {
			load() {
				this.count += 2
			},
			sendCode() {
				//发送验证码
				this.$axios
					.post('/send', {
						email: this.clockForm.email,
						type: 0
					})
					.then(resp => {
						if (resp.data.code === 200) {
							// if (resp.data.data.return_code == "00000") {
							this.$message({
								type: 'success',
								message: '验证码发送成功，请注意接收'
							})
							this.send.sendType = "success";
							this.send.sendStatus = "disabled";
							this.setInterval();
							// } else {
							// 	this.$message.error("验证码发送错误，错误代码:" + resp.data.data.return_code);
							// }
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
					})
			},
			addClock(plane) {
				if(new Date().getTime() > new Date(this.$route.query.startTime).getTime()){
					this.$message.error("该航班已经起飞，请注意时间");
						return false;
				}
				this.$axios
					.get('/authentication')
					.then(resp => {
						//直接添加提醒
						if (resp.data.code === 200) {
							var data = {
								fightId: plane.fid,
								number: plane.number,
								status: 0,
								type: 0
							}
							this.$axios
								.post('/clock', data)
								.then(resp => {
									if (resp.data.code === 200) {
										this.$message({
											type: 'success',
											message: resp.data.msg
										})
									} else {
										this.$message.error("发生错误，请联系管理员");
									}
								})
							//未绑定邮箱
						} else if (resp.data.code === 400) {
							this.dialogTableVisible = true;
							this.dialogFormVisible = true;
							this.clockForm.plane = plane;
						} else {
							this.$message.error("请先登录");
						}
					})
			},
			submitForm(formName) {
				if(Number.parseInt(this.clockForm.code)=="NaN"){
					this.$message.error("请填入正确的验证码格式");
					return false
				}
				this.$axios
					.post('/email',{email:this.clockForm.email,code:this.clockForm.code})
					.then(resp => {
						if (resp.data.code === 200) {
						var data = {
							fightId: this.clockForm.plane.fid,
							number: this.clockForm.plane.number,
							status: 0,
							type: 0
						}
						this.$refs[formName].validate((valid) => {
							if (valid) {
								//提交表单
								this.$axios
									.post('/clock', data)
									.then(resp => {
										if (resp.data.code === 200) {
											this.$message({
												type: 'success',
												message: resp.data.msg
											})
										} else {
											this.$message.error("发生错误，请联系管理员");
										}
									})
								//添加成功
								this.closeForm(formName);
							} else {
								return false;
							}
						})
						}  else {
							this.$message.error(resp.data.msg);
						}
					})
			},
			closeForm(formName) {
				clearInterval(this.interval);
				this.dialogFormVisible = false;
				this.send.sendType = "";
				this.send.sendStatus = false;
				this.send.sendMessage = "发送验证码";
			},
			setInterval() {
				var time = 60;
				this.interval = setInterval(() => {
					this.send.sendMessage = time;
					time -= 1;
					if (time === -1) {
						clearInterval(this.interval);
						this.send.sendType = "";
						this.send.sendStatus = false;
						this.send.sendMessage = "重新发送验证码";
					}
				}, 1000)
			},
			sortStartTime() {
				this.$axios
					.post('/query', {
						startCity: this.$route.query.startCity,
						endCity: this.$route.query.endCity,
						startTime: this.$route.query.startTime,
						endTime: this.$route.query.endTime,
						key:this.$route.query.key,
						orderByField:'start_time',
						isAsc:this.sortStartTimeFlag
					})
					.then(resp => {
						if (resp.data.code === 200) {
							this.results = resp.data.data.records
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
						if(this.sortStartTimeFlag){
							this.sortStartTimeFlag = false
						}else{
							this.sortStartTimeFlag = true
						}
					})
			},
			sortEndTime() {
				this.$axios
					.post('/query', {
						startCity: this.$route.query.startCity,
						endCity: this.$route.query.endCity,
						startTime: this.$route.query.startTime,
						endTime: this.$route.query.endTime,
						key:this.$route.query.key,
						orderByField:'end_time',
						isAsc:this.sortEndTimeFlag
					})
					.then(resp => {
						if (resp.data.code === 200) {
							this.results = resp.data.data.records
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
						if(this.sortEndTimeFlag){
							this.sortEndTimeFlag = false
						}else{
							this.sortEndTimeFlag = true
						}
					})
			},
			sortUseTime() {
				this.$axios
					.post('/query', {
						key: this.key,
						orderByField: 'startTime'
					})
					.then(resp => {
						if (resp.data.code === 200) {
							this.results = resp.data.data.records
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
					})
			},
			sortPrice() {
				this.$axios
					.post('/query', {
						startCity: this.$route.query.startCity,
						endCity: this.$route.query.endCity,
						startTime: this.$route.query.startTime,
						endTime: this.$route.query.endTime,
						key:this.$route.query.key,
						orderByField: 'price',
						isAsc:this.sortPriceFlag
					})
					.then(resp => {
						if (resp.data.code === 200) {
							this.results = resp.data.data.records
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
						if(this.sortPriceFlag){
							this.sortPriceFlag = false
						}else{
							this.sortPriceFlag = true
						}
					})
			}
		},
		filters: {
			formatDate(time) {
				var date = new Date(time);
				return formatRiQi(date);
			},
			subString(value) {
				if (value != "") {
					return value.substr(11);
				} else {
					return value;
				}

			}
		}
	}
</script>

<style scoped="scoped">
	.big {
		font-size: 26px;
	}

	.red {
		color: orangered;
	}

	i {
		display: block;
		font-size: 20px;
	}

	.right-icon {
		display: block;
		width: 110px;
		margin-top: 20px;
		height: 14px;
	}
</style>
