import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		user: {
			token: window.localStorage.getItem('user' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('user' ||
				'[]')).token,
			username: window.localStorage.getItem('user' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('user' ||
				'[]')).username,
		},
		sessionUser: {
			token: window.sessionStorage.getItem('user' || '[]') == null ? '' : JSON.parse(window.sessionStorage.getItem(
				'user' ||
				'[]')).token,
			username: window.sessionStorage.getItem('user' || '[]') == null ? '' : JSON.parse(window.sessionStorage.getItem(
				'user' ||
				'[]')).username,
		},
		adminMenus: []
	},
	mutations: {
		initAdminMenu(state, menus) {
			state.adminMenus = menus
		},
		login(state, data) {
			state.sessionUser.token = data.token
			state.sessionUser.username = data.username
			window.sessionStorage.setItem('user', JSON.stringify(data))
		},
		logout(state) {
			// 注意不能用 null 清除，否则将无法判断 user 里具体的内容
			state.user = []
			state.sessionUser = []
			window.localStorage.removeItem('user')
			window.sessionStorage.removeItem('user')
		},
		remember(state,data){
			state.user.token = data.token
			state.user.username = data.username
			window.localStorage.setItem('user', JSON.stringify(data))
		}
	},
	actions: {}
})
