<template>
	<div id="app" class="page-component__scroll el-scrollbar">
		<router-view :key="this.$router.push"/>
	</div>
</template>

<script>
	export default {
		name: 'app',
		watch: {
		        $route: function (to, from) {
					console.log(this.$pc)
		            if (to.name.indexOf('_m') > 0 && this.$pc) {
		                this.$router.replace(to.name.split('_m')[0])
		            } else if (to.name.indexOf('_m') < 0 && !this.$pc) {
		                this.$router.replace(to.name + '_m')
		            }
		        }
		    },
	}
</script>

<style>
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		min-height: 100%;
		/* text-align: center;
		color: #2c3e50; */
	}
</style>
