<template>
	<van-panel title="国内热门航班">
		<van-list>
			<van-swipe-cell :before-close="addClock" v-for="plane in chinaPlane">
				<template slot="left">
					<van-button type="primary" text="详情" />
				</template>
				<van-cell center size="large" :border="false" :value="plane.price+'￥起'">
					<!-- 使用 title 插槽来自定义标题 -->
					<template slot="title">
						<van-row>
							<van-col span="8">
								<van-image round width="5rem" height="5rem" :src="plane.icon" />
							</van-col>
							<van-col span="16">
								<van-col span="24">
									<van-tag type="warning" size="large">{{plane.startCity}}</van-tag>
									<van-icon name="exchange" />
									<van-tag type="warning" size="large">{{plane.endCity}}</van-tag>
								</van-col>
								<van-col span="24">
									<span>{{plane.startTime}}</span>
								</van-col>
							</van-col>
						</van-row>
					</template>
				</van-cell>
				<template slot="right">
					<van-button type="primary" text="添加提醒" />
				</template>
			</van-swipe-cell>
		</van-list>
	</van-panel>
</template>

<script>
	export default {
		name: 'ChinaHot',
		data(){
			return {
				userCity:'武汉',
				hotCity:'深圳',
				chinaPlane:''
			}
		},
		created() {
			var _this = this;
			this.$axios
				.post('/china_plane', {
					currentPage: 1,
					pageSize: 4,
					startCity: this.userCity,
					endCity: this.hotCity,
					orderByField: 'start_time'
				})
				.then(resp => {
					if (resp.data.code === 200) {
						_this.chinaPlane = resp.data.data;
					}
				})
				.catch(failResponse => {})
		},
		methods: {
			addClock({
				position,
				instance
			}) {
				switch (position) {
					case 'left':
					case 'cell':
					case 'outside':
						instance.close();
						break;
					case 'right':
						Dialog.confirm({
							message: '确定删除吗？'
						}).then(() => {
							instance.close();
						});
						break;
				}
			},
		}
	}
</script>

<style scoped="scoped">
	.van-cell__value {
		color: indianred;
		font-size: 17px;
		font-weight: 600;
	}
</style>
