import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/Index'
import Search from '@/components/home/Search'
import Login from '@/components/home/Login'
import AdminLogin from '@/components/admin/Login'
import AdminIndex from '@/components/admin/Index'
import AdminHome from '@/components/admin/Home'
import CompanyAdd from '@/components/admin/company/Add'
import CompanyShow from '@/components/admin/company/Show'
import PlaneAdd from '@/components/admin/plane/Add'
import PlaneShow from '@/components/admin/plane/Show'
import NewsAdd from '@/components/admin/news/Add'
import NewsShow from '@/components/admin/news/Show'
import FightAdd from '@/components/admin/fight/Add'
import FightShow from '@/components/admin/fight/Show'
import RoleAdd from '@/components/admin/role/Add'
import RoleShow from '@/components/admin/role/Show'
import PermissionAdd from '@/components/admin/permission/Add'
import PermissionShow from '@/components/admin/permission/Show'
import TaskShow from '@/components/admin/task/Show'
import NewShow from '@/components/home/News'
import NotFound from '@/components/error/404'
import MyClock from '@/components/home/MyClock'
import HomeM from '@/components/mobile/home/Index'

Vue.use(Router)

export default new Router({
	//mode: 'history',
	routes: [{
		path: '/',
		name: 'index',
		component: Home
	}, {
		path: '/login',
		name: 'login',
		component: Login
	}, {
		//home页面不需要被访问,只是作为其他组件的父组件
		path: '/index',
		name: 'index',
		component: Home
	}, {
		path: '/news/*',
		name: 'NewShow',
		component: NewShow
	}, {
		path: '/search',
		name: 'Search',
		component: Search,
		meta: {
			requireAuth: true
		},

	}, {
		path: '/myClock',
		name: 'myClock',
		component: MyClock,
		meta: {
			requireAuth: true
		},

	}, {
		path: '/admin/login',
		name: 'AdminLogin',
		component: AdminLogin
	}, {
		path: '/admin',
		name: 'Admin',
		redirect: '/admin/index',
		component: AdminIndex,
		children: [{
			path: '/admin/index',
			name: 'admin',
			component: AdminHome
		}, {
			path: '/admin/company/add',
			name: 'CompanyAdd',
			component: CompanyAdd
		}, {
			path: '/admin/company/show',
			name: 'CompanyShow',
			component: CompanyShow
		}, {
			path: '/admin/plane/add',
			name: 'PlaneAdd',
			component: PlaneAdd
		}, {
			path: '/admin/plane/show',
			name: 'PlaneShow',
			component: PlaneShow
		}, {
			path: '/admin/news/add',
			name: 'NewsAdd',
			component: NewsAdd
		}, {
			path: '/admin/news/show',
			name: 'NewsShow',
			component: NewsShow
		}, {
			path: '/admin/fight/add',
			name: 'FightAdd',
			component: FightAdd
		}, {
			path: '/admin/fight/show',
			name: 'FightShow',
			component: FightShow
		}, {
			path: '/admin/task/show',
			name: 'TaskShow',
			component: TaskShow
		}, {
			path: '/admin/role/add',
			name: 'RoleAdd',
			component: RoleAdd
		}, {
			path: '/admin/role/show',
			name: 'RoleShow',
			component: RoleShow
		}, {
			path: '/admin/permission/add',
			name: 'PermissionAdd',
			component: PermissionAdd
		}, {
			path: '/admin/permission/show',
			name: 'PermissionShow',
			component: PermissionShow
		}]
	}, {
		path: '/index_m',
		name: 'index_m',
		component: HomeM
	}, {
		path: '/login_m',
		name: 'login_m',
		component: HomeM
	}, {
		path: '*',
		name: '404',
		component: NotFound
	} ]
})
