<template>
	<div>
		<span is-link @click="showPopup">{{currentDate}}</span>
		<van-popup v-model="picker" position="bottom">
			<van-datetime-picker v-model="confirmDate" type="datetime" :min-date="minDate" :max-date="maxDate" @cancel="onCancel" @confirm="confirm"
			 :formatter="formatter" />
		</van-popup>
	</div>
</template>

<script>
	export default {
		name: 'DateTimePicker',
		data() {
			return {
				picker: false,
				minDate: new Date(2019, 0, 1),
				maxDate: new Date(2025, 10, 1),
				currentDate: this.dateFormatter(new Date()),
				confirmDate:''
			};
		},
		methods: {
			showPopup() {
				this.picker = true;
			},
			onCancel() {
				this.picker = false
			},
			confirm(data) {
				this.confirmDate = this.dateFormatter(new Date(data))
				this.currentDate = this.confirmDate
				this.picker = false
			},
			formatter(type, value) {
				if (type === 'year') {
					return `${value}年`;
				} else if (type === 'month') {
					return `${value}月`
				} else if (type === 'day') {
					return `${value}日`
				}
				return value;
			},
			dateFormatter(date){
				let h = date.getHours();
				let m = date.getMonth() + 1;
				let year = date.getFullYear();
				let d = date.getDate();
				let min = date.getMinutes();
				if (h >= 0 && h <= 9) {
					h = "0" + h;
				}
				if (m >= 1 && m <= 9) {
					m = "0" + m;
				}
				if (d >= 0 && d <= 9) {
					d = "0" + d;
				}
				if (min >= 0 && min <= 9) {
					min = "0" + min;
				}
				console.log(year + '-' + m + '-' + d + ' ' + h + ':' + min)
				return year + '-' + m + '-' + d + ' ' + h + ':' + min;
			}
		}
	};
</script>

