<template>
	<div>
		<el-row :gutter="20">
			<el-col :span="12">
				<el-divider content-position="left">热门城市搜索占比</el-divider>
				<div ref='myEchart' style="width:400px;height:400px"></div>
			</el-col>
			<el-col :span="12">
				<el-divider content-position="left">注册人数</el-divider>
				<div ref='echart2' style="width:400px;height:400px"></div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import echarts from 'echarts'
	export default {
		data() {
			return {
				dataList: [{
						value: 1,
						name: '0-20分'
					},
					{
						value: 3,
						name: '21-50分'
					},
					{
						value: 24,
						name: '51-70分'
					},
					{
						value: 7,
						name: '71-80分'
					},
					{
						value: 3,
						name: '81-100分'
					},
				]
			}
		},
		methods: {
			// 图表初始化数据
			initChart() {
				let myChart = echarts.init(this.$refs.myEchart);
				myChart.setOption({
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b}: {c} <br/> ({d}%)"
					},
					series: [{
						name: '学生作业提交率',
						type: 'pie',
						radius: '80%',
						center: ['52%', '53%'],
						avoidLabelOverlap: false,
						data: this.dataList,
						itemStyle: {
							emphasis: {
								shadowBlur: 5,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.3)'
							}
						}
					}]
				})
				let echart2 = echarts.init(this.$refs.echart2);
				echart2.setOption({
					xAxis: {
						type: 'category',
						data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
					},
					yAxis: {
						type: 'value'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b}: {c} <br/> ({d}%)"
					},
					series: [{
						data: [820, 932, 901, 934, 1290, 1330, 1320],
						type: 'line',
						smooth: true
					}]
				})
			}
		},
		mounted() {
			let obj = this.$refs.myEchart;
			if (obj) {
				this.initChart();
			}
		}
	}
</script>

