<template>
	<div>
		<van-icon name="smile-o" @click="show = true" />
		<van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
		<van-swipe :autoplay="3000" indicator-color="white" style="height: 11rem;">
			<van-swipe-item style="overflow: hidden;">
				<img src="@/assets/img/1_m.jpg" />
			</van-swipe-item>
			<van-swipe-item>
				<img src="@/assets/img/1_m.jpg" />
			</van-swipe-item>
			<van-swipe-item>
				<img src="@/assets/img/1_m.jpg" />
			</van-swipe-item>
		</van-swipe>
	</div>

</template>

<script>
	export default {
		name: 'Header',
		data() {
			return {
				show: false,
				actions: [{
						name: '登录'
					},
					{
						name: '选项'
					},
					{
						name: '选项',
						subname: '描述信息'
					}
				]
			};
		},

		methods: {
			onSelect(item) {
				// 默认情况下，点击选项时不会自动关闭菜单
				// 可以通过 close-on-click-action 属性开启自动关闭
				this.show = false;
				Toast(item.name);
			}
		}
	}
</script>

<style scoped="scoped">
	.van-icon {
		position: absolute;
		z-index: 999;
		display: block;
		font-size: 1.9375rem;
		margin-top: 6px;
	}
</style>
