<template>
	<body id="paper">
		<el-form :model="loginForm" :rules="rules" class="login-container" label-position="left" label-width="0px" v-loading="loading">
			<h3 class="login_title">系统登录</h3>
			<el-form-item prop="account">
				<el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="支持邮箱/手机号登录"></el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
			</el-form-item>
			<el-checkbox class="login_remember" v-model="checked" label-position="left"><span style="color: #505458">记住密码</span></el-checkbox>
			<el-form-item style="width: 100%">
				<el-button type="primary" style="width: 40%;background: #505458;border: none;float: right;" v-on:click="login">登录</el-button>
				<el-button @click="dialogFormVisible = true">注册</el-button>
			</el-form-item>
		</el-form>
		<el-dialog :visible.sync="dialogFormVisible" width="450px" :modal-append-to-body='false'>
			<el-tabs v-model="activeName" :stretch="true">
				<el-tab-pane label="手机号注册" name="phone">
					<el-form :model="phoneFrom" ref="phoneFrom" :rules="phoneRules" :hide-required-asterisk="false" label-width="0px"
					 style="margin:0 50px;">
						<el-form-item prop="phone">
							<el-input v-model="phoneFrom.phone" autocomplete="off" placeholder="中国大陆手机号"></el-input>
						</el-form-item>
						<el-form-item prop="code">
							<el-row :gutter="20">
								<el-col :span="14">
									<el-input v-model="phoneFrom.code" autocomplete="off" placeholder="验证码"></el-input>
								</el-col>
								<el-col :span="10">
									<el-button :type="send.sendType" :model="send" :disabled="send.sendStatus" @click="sendCode" :key="send.sendMessage">
										{{ send.sendMessage }}
									</el-button>
								</el-col>
							</el-row>
						</el-form-item>
						<el-form-item prop="username">
							<el-input v-model="phoneFrom.username" placeholder="昵称"></el-input>
						</el-form-item>
						<el-form-item prop="password">
							<el-input type="password" v-model="phoneFrom.password" placeholder="密码"></el-input>
						</el-form-item>
						<el-form-item>
							<el-divider>第三方登录</el-divider>
							<el-link :underline="false" href="http://47.107.131.246:8443/api/getQQCode" target="_blank">
								<i class="iconfont icon-qq1"></i>
							</el-link>
							<el-link :underline="false" href="https://element.eleme.io" target="_blank">
								<i class="iconfont icon-weixin1"></i>
							</el-link>
							<el-link :underline="false" href="https://element.eleme.io" target="_blank">
								<i class="iconfont icon-github1"></i>
							</el-link>
						</el-form-item>
						<el-form-item>
							<el-button style="width: 100%;" type="info" @click="submit('phoneFrom')">注 册</el-button>
						</el-form-item>
					</el-form>
				</el-tab-pane>
				<el-tab-pane label="邮箱注册" name="email">
					<el-form :model="emailFrom" ref="emailFrom" :rules="emailRules" :hide-required-asterisk="false" label-width="0px"
					 style="margin:0 50px;">
						<el-form-item prop="email">
							<el-input v-model="emailFrom.email" autocomplete="off" placeholder="邮箱"></el-input>
						</el-form-item>
						<el-form-item prop="username">
							<el-input v-model="emailFrom.username" autocomplete="off" placeholder="昵称"></el-input>
						</el-form-item>
						<el-form-item prop="password">
							<el-input type="password" v-model="emailFrom.password" placeholder="密码"></el-input>
						</el-form-item>
						<el-form-item>
							<el-divider>第三方登录</el-divider>
							<el-link :underline="false" href="/getQQCode" target="_blank">
								<i class="iconfont icon-qq1"></i>
							</el-link>
							<el-link :underline="false" href="https://element.eleme.io" target="_blank">
								<i class="iconfont icon-weixin1"></i>
							</el-link>
							<el-link :underline="false" href="https://element.eleme.io" target="_blank">
								<i class="iconfont icon-github1"></i>
							</el-link>
						</el-form-item>
						<el-form-item>
							<el-button style="width: 100%;" type="info" @click="submit('emailFrom')">注 册</el-button>
						</el-form-item>
					</el-form>
				</el-tab-pane>
			</el-tabs>
		</el-dialog>
	</body>
</template>
<script>
	import md5 from 'js-md5';//md5加密
	import sha3 from 'js-sha3';
	import base64 from 'js-base64';
	export default {
		data() {
			var validatePhone = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入手机号'));
				} else if (!(/^1[3456789]\d{9}$/.test(value))) {
					callback(new Error('请输入正确手机号'));
				} else {
					callback();
				}
			};
			return {
				dialogFormVisible: false,
				rules: {
					account: [{
						required: true,
						message: '账号不能为空',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '密码不能为空',
						trigger: 'blur'
					}]
				},
				phoneRules: {
					phone: [{
						validator: validatePhone,
						trigger: 'blur'
					}],
					code: [{
						required: true,
						message: '验证码不能为空',
						trigger: 'blur'
					}],
					username: [{
						required: true,
						message: '昵称不能为空',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '密码不能为空',
						trigger: 'blur'
					}]
				},
				emailRules: {
					email: [{
						required: true,
						message: '邮箱不能为空',
						trigger: 'blur'
					}, {
						type: 'email',
						required: true,
						message: '请输入正确邮箱',
						trigger: 'change'
					}],
					username: [{
						required: true,
						message: '用户名不能为空',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '密码不能为空',
						trigger: 'blur'
					}]
				},
				send: {
					sendType: '',
					sendStatus: false,
					sendMessage: '发送验证码',
				},
				checked: true,
				loginForm: {
					username: '',
					password: ''
				},
				phoneFrom: {
					phone: '',
					code: '',
					username: '',
					password: ''
				},
				emailFrom: {
					email: '',
					code: '',
					username: '',
					password: ''
				},
				loading: false,
				activeName: 'phone'
			}
		},
		methods: {
			sendCode() {
				var phone = this.phoneFrom.phone;
				if (phone == '') {
					this.$message.error("请填写手机号");
				}
				//发送验证码
				this.$axios
					.post('/send', {
						phone: phone
					})
					.then(resp => {
						if (resp.data.code === 200) {
							// if (resp.data.data.return_code == "00000") {
							this.$message({
								type: 'success',
								message: '验证码发送成功，请注意接收'
							})
							this.send.sendType = "success";
							this.send.sendStatus = "disabled";
							this.setInterval();
							// }else{
							// 	this.$message.error("验证码发送错误，错误代码:"+resp.data.data.return_code);
							// }
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("验证码发送错误");
						}
					})
			},
			setInterval() {
				var time = 60;
				this.interval = setInterval(() => {
					this.send.sendMessage = time;
					time -= 1;
					if (time === -1) {
						clearInterval(this.interval);
						this.send.sendType = "";
						this.send.sendStatus = false;
						this.send.sendMessage = "重新发送验证码";
					}
				}, 1000)
			},
			submit(formName) {
				if(formName=="phoneFrom"){
					var data = {
						username: base64.Base64.encode(this.phoneFrom.username),
						phone: base64.Base64.encode(this.phoneFrom.phone),
						password: sha3.sha3_256(md5(this.phoneFrom.password))
					}
				}else{
					var data = {
						username: base64.Base64.encode(this.emailFrom.username),
						email: base64.Base64.encode(this.emailFrom.email),
						password: sha3.sha3_256(md5(this.emailFrom.password))
					}
				}
				
				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.$axios
							.post('/register',data)
							.then(resp => {
								if (resp.data.code === 200) {
									var data = resp.data.msg;
									this.$message({
										message: data,
										type: 'success'
									});
									var store = {
										token:resp.data.data.token,
										username:resp.data.data.username
									}
									this.$store.commit('login', store)
									setTimeout(function(){
										var path = this.$route.query.redirect
										this.$router.replace({
											path: path === '#/login' || path === undefined ? '/' : path
										})
									},20)
								}else{
									this.$message.error(resp.data.msg);
								}
							})
							.catch(failResponse => {
							console.log(failResponse)
							})
					} else {
						alert('error submit!!');
						return false;
					}
				});
			},
			login() {
				var _this = this
				var password = sha3.sha3_256(md5(this.loginForm.password));
				var email = base64.Base64.encode(this.loginForm.account);
				this.$axios
					.post('/login', email+password )
					.then(resp => {
						if (resp.data.code === 200) {
							var data = resp.data.msg
							this.$message({
								message: data,
								type: 'success'
							})
							// 记住密码
							var store = {
								token:resp.data.data.token,
								username:resp.data.data.username
							}
							if(this.checked){
								_this.$store.commit('remember', store)
							}else{
								_this.$store.commit('login', store)
							}
							var path = _this.$route.query.redirect
							_this.$router.replace({
								path: path === 'login' || path === undefined ? '/' : path
							})
						} else if (resp.data.code === 500) {
							this.$message.error(resp.data.msg);
						} else {
							this.$message.error("发生错误，联系管理员");
						}
					})
					.catch(failResponse => {})
			}
		}
	}
</script>
<style scoped="scoped">
	#paper {
		background: url("../../assets/img/bg/eva1.jpg") no-repeat;
		background-position: center;
		height: 100%;
		width: 100%;
		background-size: cover;
		position: fixed;
	}

	body {
		margin: -5px 0px;
	}
	.el-form-item{
		text-align: center;
	}

	.el-link i {
		font-size: 25px;
		width: 55px;
		display: block;
	}

	.login-container {
		border-radius: 15px;
		background-clip: padding-box;
		margin: 90px auto;
		width: 350px;
		padding: 35px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		box-shadow: 0 0 25px #cac6c6;
	}

	.login_title {
		margin: 0px auto 40px auto;
		text-align: center;
		color: #505458;
	}

	.login_remember {
		margin: 0px 0px 35px 0px;
		text-align: left;
	}

	/*.login_button {*/
	/*background: #505458;*/
	/*}*/
	/*el_checkbox {*/
	/*background: #505458;*/
	/*}*/
</style>
